.navbar {
  /* background-color: #009bac; */
  background-image: url('https://static.vecteezy.com/system/resources/previews/001/933/301/original/abstract-fluid-waves-blue-background-vector.jpg');
  background-size: cover;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  
}

.menu-bars {
  margin-left: 2rem;
  margin-bottom: 0.8rem;
  font-size: 2rem;
  background: none;
  font-family: 'Ubuntu',Arial,sans-serif;
  transition: 0.5s;
}

.menu-bars:hover {
  transform: scale(1.1);
  transition: 0.5s;
}

.nav-menu {
  background-color: #1f4474;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
  font-family: 'Ubuntu',Arial,sans-serif;
}

.nav-text a:hover {
  background-color: #6792af;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #1f4474;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

span {
  margin-left: 16px;
}
